.navbar {
  background-color: rgba(1, 0, 40, 0.7);
}
.navbar .navbar-item img {
  max-height: 3.75rem;
}
.navbar .navbar-brand a.navbar-item {
  display: flex;
  align-items: flex-end;
  gap: 5px;
  color: #FFF;
  background-color: rgb(1, 0, 40);
}
.navbar .navbar-brand a.navbar-item .brand {
  font-family: "Archivo Black", sans-serif;
  font-size: 36px;
  line-height: 36px;
}
.navbar .navbar-brand a.navbar-item:hover, .navbar .navbar-brand a.navbar-item:active {
  color: #FFF;
}